@import url(https://fonts.googleapis.com/css2?family=Caveat&family=Federant&family=Roboto&family=Special+Elite&display=swap);
@import url(//netdna.bootstrapcdn.com/font-awesome/3.0/css/font-awesome.css);

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.card {
    display: flex;
    flex-direction: column;
    width: 80%;
    border: 2px solid black;
    border-radius: 10px;
    padding: 1.5rem;
    margin: 1rem;
    /* box-shadow: 0 16px 20px 0; */
}

.headerSec {
    display: flex;
    background-color: rgba(20, 204, 179, 0.986);;
    align-items: center;
    border-radius: 5px;
    margin-bottom: 1rem;
    /* color: white; */
}

.avatar{
    border: 1px solid black;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px;
}

.userTime {
    margin-left: 0.5rem;
}

#timeStamp {
    font-size: 16px;
}

.likes {
    display: flex;
    margin-top: 1rem;
    margin-bottom: 1rem;
}
img {
    display: inline-block;
}

 .comment {
    display: flex;
    align-items: center;
}

.buttons {
    display: flex;
    justify-content: space-around;
}

hr{
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
}

.buttons button{
    cursor: pointer;
    padding: 0px;
    background-color: white;
    border: none;
}


textarea {
    width: 100%;
    height: 100%;
    resize: none;
    margin-top: 1rem;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: var(--font-2);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  form{
    color: var(--white);
    padding: 4rem;
    font-size: 15px;
}

.createPost{
    display: flex;
    flex-wrap: wrap;
    padding: 30px;
    justify-content: center;
    box-shadow: 0 1px 2px 1px white, 0 1px 2px 0 ;
    grid-gap: 10px;
    gap: 10px;
}


/* PostWrittingCard  icon */
.createPost .icon{
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background-color:#b9b7b7;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 40px;
    background-color: rgba(20, 204, 179, 0.986);
    
}


/* PostWrittingCard  input */

.createPost input {
    background: #F0F2F5;
    border: none;
    border-radius: 80px;
    margin: 8px auto;
    margin-top: 20px;
    outline-style:none;
    outline: none;
}

input[type="button"] {
    width: 10rem;
    height: 40px;
    font-size: 20px;
    margin-top: 25px;
    cursor: pointer;
    background-color: rgba(20, 204, 179, 0.986);

}
.createPost .button{
    padding-left: 30px;
}
input[type="text"] {
    width: 30rem;
    height: 50px;
    font-size: 20px;
    padding: 8px 12px;
}

.home{
    min-height: calc(100vh -500px);
}
header{
    margin: 0.5rem 4rem;
    padding: 1rem;
    display: flex;
    flex-wrap: wrap;
    box-shadow: 0 1px 2px 1px white, 0 1px 2px 0 ;
    justify-content: center;
    color: rgba(20, 204, 179, 0.986);
    margin-bottom: 0;
}

header h2{
   margin:20px;
    font-family: 'Federant', cursive;
  
}

header input[type="text"]  {
    background: #F0F2F5;
    border: none;
    border-radius: 80px;
    margin-top: 10px;
    outline-style:none;
    outline: none;
    width: 20rem;
    height: 30px;
    font-size: 20px;
    margin: 20px;
    
}

header input::-webkit-input-placeholder{
    font-family: FontAwesome;
    color:rgba(20, 204, 179, 0.986);;
    -webkit-transform: rotateY(180deg);;
            transform: rotateY(180deg);
    opacity: 0.2;
}

header input:-ms-input-placeholder{
    font-family: FontAwesome;
    color:rgba(20, 204, 179, 0.986);;
    transform: rotateY(180deg);
    opacity: 0.2;
}

header input::placeholder{
    font-family: FontAwesome;
    color:rgba(20, 204, 179, 0.986);;
    -webkit-transform: rotateY(180deg);;
            transform: rotateY(180deg);
    opacity: 0.2;
}

.inputSearch{
    margin-left: -30px;
}
header .icon{
    font-size: 40px;
    margin: 10px 50px;
    color: rgba(20, 204, 179, 0.986);
}

.Nav{
    display: flex;
    flex-wrap: wrap;
}

.notification{
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background-color: red;
    color: white;
    text-align: center;
}

header .bell{
    font-size: 20px;
    padding: 10px;
    margin: -15px;
    margin-top: 5px;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    background: #F0F2F5;
    margin-left: 300px;
    color: rgba(20, 204, 179, 0.986);

}
/* ####### Footer ###### */
footer{
    box-shadow: 0 1px 2px 1px white, 0 1px 2px 0 ;
    /* margin: 0.5rem 4rem; */
    color:#800080 ;
    bottom:0;
    width:100%;
    text-align: center;
    /* position: absolute; */
}

footer p{ 
    font-size: 1rem;
    font-weight: 600;
    padding: 30px;
}





